  line-height: 25px;
  vertical-align: middle;
  text-decoration: none;
  color: #222;
  font-weight: 400;
  word-break: keep-all;
  -webkit-overflow-scrolling: touch;
}

/* a Link style */
a {
  text-decoration: none;
  color: #555 !important;
}
a:active,
a:link,
a:visited {
  outline: 0;
  text-decoration: none;
}
a:hover {
  color: #1c91e2;
  outline: 0;
  text-decoration: underline;
}

/* etc */
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
address,
h1,
h2,
h3,
h4,
h5 {
  font-style: normal;
  font-weight: 500;
}
img {
