}
.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn:active:focus,
.btn:focus,
:active,
:focus,
:visited,
a,
a:active,
a:focus,
a:visited {
  outline: 0;
}
em {
  font-style: normal;
  font-weight: 400;
}
ol,
ul,
li {
  list-style: none;
  margin-bottom: 0 !important;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

